import { createStore } from 'vuex';

const store = createStore({
    state() {
        return {
            //    
            userId : null,
            userNickname : null,
            profileImg : null,
            LoadingStatus: false,


            //(Global) 
            gModalState : false,
            gModalMsg : "",
            gModalSize : "",
            gModalBg : "",


        }
    },
    mutations : {
        // 
        userLogin(state, user) {
            if(user == null) {
                state.userNickname = null;
                state.userId = null;
                state.profileImg = null;
            } else {
                state.userNickname = user.nickname;
                state.userId = user.id;
                state.profileImg = user.profileImg;
            }
            
        },
        
        //
        startSpinner(state){
            state.LoadingStatus = true;
        },
        endSpinner(state){
            state.LoadingStatus = false;
        },


        // Modal
        gModalOn(state, info){
            state.gModalSize = info.size;
            state.gModalMsg = info.msg;
            state.gModalBg = info.bg;
            state.gModalState = true;
        },
        gModalOff(state){
            state.gModalState = false;
        },

        //  
        setNovelList(state, data) {
            state.novelList = data;
        },

    },
})

export default store;