<template>
  <div>
    <!--    modal_frame,
         .  -->
    <div v-bind:class="{ post_modal_frame_big: true }">
      <!--     modal_inner,     
       .-->
      <div class="post_modal_inner">
        <span> ..</span>
        <div class="post_modal_content"><span v-html="modaldatasend.content"></span></div>
        <!--  -->
      </div>
      <!--    (,  )-->
      <div class="post_modal_btn_position">
        <div class="post_modal_btn"><span></span></div>
        <div class="post_modal_btn"><span></span></div>
        <div class="post_modal_btn_red" @click="modalremove"><span></span></div>
        <div class="post_modal_btn" id="cancel" @click="$emit('closemodal', open)"><span></span></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "vsn_modal_universal",

  components: {},
  props: {
    open : Boolean,
    modaldatasend : Object,
    datasend : Object,
  },
  methods: {
    modalremove() {
      this.$emit('deletedata');
      this.$emit('closemodal', open);
    }
  }
};
</script>
<style>
.post_modal_content {
  position:relative;
  background-color:gray;
  border-radius: 15px;
  height: 200px;
  margin-top: 10px;
  overflow-y:scroll;
  word-wrap: break-word;
  
}
.post_modal_content span {
  position: absolute;
  margin-top: 10px;
  width: 95%;
  left: 50%;
  transform: translateX(-50%);
}
.post_modal_frame_big {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 700px;
  height: 310px;
  border-radius: 25px;
  background: #2a2a2a;
  animation-duration: 0.7s;
  animation-name: opening;
  z-index: 2;
  opacity: 1;
}
.post_modal_inner {
  color: white;
  position: fixed;
  width: 90%;
  left: 5%;
  top: 5%; 
}
.post_modal_btn {
    position: relative;
    width: 100px;
    height: 30px;
    background-color:#2872f9;
    color: white;
    text-align: center;
    font-size: 1.0em;
    border-radius: 12px;
    cursor: pointer;
    margin-right: 10px;
    padding-top: 3px;
}
.post_modal_btn_red {
    position: relative;
    width: 100px;
    height: 30px;
    background-color:rgb(255, 71, 71);
    color: white;
    text-align: center;
    font-size: 1.0em;
    border-radius: 12px;
    cursor: pointer;
    padding-top: 3px;
}
.post_modal_btn_position {
  display:flex;
  position:relative;
  width: 90%;
  margin: 0 auto; 
  top: 90%;
  transform: translateY(-50%);
}
#cancel{
  left: 33.5%;
}
/* ,*/
/*  opening*/
@keyframes opening {
  from {
    top: 150%;
    opacity: 0;
  }

  to {
    top: 50%;
    opacity: 1;
  }
}
</style>