 <template>
<div v-if="gModalState">
  <!--       
      modal_opacity  .-->
  <div class="modal_opacity" @click="modalClose">
  </div>
  <!--    modal_frame-->
  <div v-bind:class="{[`modal_frame_${this.$store.state.gModalSize}`]:true}" v-bind:style="{backgroundImage: 'url(' + this.$store.state.gModalBg + ')'}">
    <!--     modal_inner,     
       .-->
    <div class="modal_inner">
        <span>{{this.$store.state.gModalMsg}}</span><!-- -->
    </div>
      <!--    (,  )-->
    <div class="modal_close_button">
      
      <span class="modal_close_ok" @click="modalClose()"></span>
    </div>
  </div>  
</div>
</template>

<style>
.modal_opacity{
  background: black;
  width: 100vw;
  height: 100vh;
  opacity:0;
  z-index: 100;
  position: fixed;
  animation-duration: 0.3s;
  animation-name: backgrounding_on;
  animation-fill-mode: forwards;
}
.modal_frame_normal{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  height: 200px;
  border-radius: 25px;
  background: #2a2a2a;
  animation-duration: 0.7s;
  animation-name: opening;
  z-index: 98;
  opacity: 1;
}
.modal_frame_small{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 125px;
  border-radius: 25px;
  background: #2a2a2a;
  /*  */
  animation-duration: 0.7s;  
  /*  */
  animation-name: opening;
  z-index: 98;
  opacity: 1;
}
.modal_frame_big{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 700px;
  height: 400px;
  border-radius: 25px;
  background: #2a2a2a;
  animation-duration: 0.7s;
  animation-name: opening;
  z-index: 98;
  opacity: 1;
}
.modal_inner{
  color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal_close_button{
  width: 40%;
  height: 15%;
  background: #2872f9;
  border-radius: 20px;
  z-index: 2;
  display: table;
  position: fixed;
  top: 85%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.modal_close_ok{
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  color: white;
  cursor: pointer;
}
/* ,*/
  /*  opening*/
@keyframes opening {
  from {
    top:150%;
    opacity: 0;
  }

  to {
    top:50%;
    opacity: 1;
  }
}

  /*  backgrounding_on*/
@keyframes backgrounding_on {
  from {
  }

  to {
    opacity: 0.3;
  }
}

</style>
<script>
export default {
  name: 'GlobalModal',
  methods : {
    modalClose() {
      this.$store.commit('gModalOff');
    }
  },
  props: {
    gModalState: {
      type: Boolean,
      required: true
    }
  }
}
  
</script>

