<template>
<ConfirmModal ref="confirmModal"></ConfirmModal>
<!-- (~) -->
  <div class="tests">
    <div class="header">
      <div class="service">
        <img class="icon" src="../../assets/icons/white/home.png" alt="logo" />
        <span class="title"></span>
        <span class="my_topic"> </span>
      </div>
    </div>
  
  <!-- ~   -->
  <div class="main-position-container">
    <!-- ,     -->
      <div class="profile-container-box">

        <div class="profile-nick-line">
          <div class="col-one"></div> 
          <input id="profilenick" type="text" class="profile-nick-input" v-model="newNickname" :disabled="nicknameCng == false">

          <div v-if="nicknameCng == false">
            <button class="mypage_main-savebtn" @click="nicknameCng = true; newNickCheck = false;"></button>
          </div>
          <div v-else-if="newNickCheck == false">
            <button class="mypage_main-savebtn" @click="newNicknameCheck()"></button>
          </div>

        </div>


        <div class="profile-intro-line">
          <div class="col-one"></div>
          <textarea id="profileintro" class="profile-introduce-input" type="text" maxlength="500" v-model="newIntro"></textarea>
          <!-- <div class="intro-cont-align"><span>{{newIntro.length}}</span>/500</div> -->
        </div>

        <!-- <button id="mypage_main-canc" @click="cancelBtn"></button> -->
        <button v-if="isSave" id="mypage_main-save" @click="editBtn()"></button>
        <button v-else id="mypage_main-save" @click="saveBtn()"></button>

        <hr>

        <div class="profile-image-line">
          <div class="col-one"> </div>

          <div class="profile-image-input" v-if="uploadImg == false">
            <img class="profile-image-input" src="@/assets/imgs/noprofile.png">
          </div>
          <div v-else>
            <img id="profile-image" class="profile-image-input" :src="newImage">
          </div>
          
          <div class="col-three">
            <input @change="upload" type="file" id="input-file" style="display:none" />
            <label class="input-file-button" for="input-file">Browse</label><br>

            <div v-if="this.profileImg != ''">
              <button class="mypage_main-save_img" @click="uploadProfileImg()"></button>
            </div>
            
            <!-- <span>512x512     <br>
             :png,jpeg,jpg,gif | > 2MB</span> -->
          </div>
        </div>



      </div>

      <!-- ,  -->
      <footer>

      </footer>
      
    </div>


  </div>
</template>

<script>
import axios from '../../axios';
import storage from '../../aws'
import ConfirmModal from '../modal/ConfirmModal.vue'
export default {
  created() {
    this.getProfile()
  },
  watch : {
    newImage() {
      if(this.newImage == 'none' || this.newImage == null) {
        this.uploadImg = false;
      } else {
        this.uploadImg = true;
      }
    }
  },
  data(){
    return{
      uploadImg: false,
      newImage : "",
      profileImg : '',

      preNickname : "",
      newNickname: "",
      newIntro: "",

      isSave : true,
      nicknameCng : false,
      newNickCheck : false
    }
  },
  methods:{
    // browse    
    upload(e){
      this.uploadImg = true;

      this.profileImg = e.target.files[0]
      var reader = new FileReader();
      reader.onload = e => {
        const previewImage = document.getElementById("profile-image")
        previewImage.src = e.target.result
      }
      reader.readAsDataURL(this.profileImg);
    },

    async uploadProfileImg() {
      var result = await storage.uploadProfileImg(this.$store.state.userId, this.profileImg);
      if(result == "ok") {
        this.$store.commit("userLogin", {
          nickname: this.$store.state.userNickname,
          id: this.$store.state.userId,
          profileImg : await storage.getUserProfileImg(this.$store.state.userId)
        });
      }
    },

    saveBtn() {
      var newContent = {
        newNickname : this.newNickname,
        newIntro : this.newIntro
      }
      axios.post('/api/mypage/mypagemain', newContent)
      .then((result) => {
        
        if(result.data.length != 0) {
          console.log(result.data)
          console.log('his')
        }
      }).catch((err) => {
        console.log(err);
      })
      this.isSave=true
    },
    //  
    getProfile() {
      axios.get('/api/mypage/mypagemain')
      .then((result)=>{
        if(result.data=="err"){
          console.log("   ");
        } else {
          this.newImage = this.$store.state.profileImg;
          this.newNickname = result.data[0].USER_NICKNAME
          this.newIntro = result.data[0].USER_INTRO
          this.preNickname = result.data[0].USER_NICKNAME;
        }
      })
    },

    newNicknameCheck() { //  
      if(this.newNickname == "") {
        this.$store.commit('gModalOn', {msg : " .", size : "normal"});
      } else if(this.newNickname == this.preNickname) {
        this.$store.commit('gModalOn', {msg : "  .", size : "normal"});
      } else {

        axios.post('/api/auth/existNicknameCheck', {newNickname : this.newNickname})
        .then(async (result)=>{
          if(result.data == "exist") {
            this.$store.commit('gModalOn', {msg : "  .", size : "normal"});
            this.newNickCheck = false;
          } else {
            var confirm = await this.$refs.confirmModal.show({
              msg : ` [${this.newNickname}]() ?`,
              size : "normal",
              btn1 : "",
              btn2 : ""
            });
            if(confirm) {
              this.newNickCheck = true;
              this.nicknameCng = false;
            } else {
              this.newNickCheck = false;
              this.nicknameCng = true;
            }
          }
        })
        .catch((err)=>{
          console.error(err);
        })

      }
    },

    //      
    editBtn(){
      if(this.newNickname == '' || this.nicknameCng == true) {
        this.$store.commit('gModalOn', {msg : "    .", size : "normal"});
      } else {
        var data = {
          newNickname : this.newNickname,
          newIntro : this.newIntro
        }
        axios.post('/api/mypage/updateProfile', data)
        .then((result)=>{
          if(result.data == "err") {
            this.$store.commit('gModalOn', {msg : "ERR :   ", size : "normal"});
          } else {
            this.$store.commit('gModalOn', {msg : " .", size : "normal"});
            this.newNickCheck = false;
            this.nicknameCng = false;
          }
        })
      }
    },

    //      
    cancelBtn(){
      const nick = document.getElementById('profilenick');
      const intro = document.getElementById('profileintro');
      const browse = document.getElementById('input-file');
      const btn = document.querySelector("#mypage_main-save");
      
      btn.innerText='';
      nick.disabled = true;
      intro.disabled = true;
      browse.disabled = true;
      
      nick.value='';
      intro.value='';
      // this.areaText = '';
      
      document.getElementById('profile-image').classList.replace('profile-image-change','profile-image-input');
      document.getElementById('profile-image').style.backgroundImage= "url('')";
      // document.getElementById('profile-image').classList.remove('profile-image-change');
      // document.getElementById('profile-image').classList.add('profile-image-input');
      
    },

    routerPush(link){
      this.$router.push(link);
    },
    
  },
  components : {
    ConfirmModal
  }
}

</script>

<style>

.mainBtnArea{
  display: flex;
  text-align: center;
  margin-top: 2%;
  line-height: 42px;
}

.my_topic{
  display: block;
  width: 150px;
  color: white;
  position: relative;
  top: 30px;
  left: calc(100% - 350px);
  font-size: 1.2em;
}
/*   */
.profile-nick-line,
.profile-image-line
{
  display: flex;
}
.profile-intro-line {
  position: relative;
}

/* browse  */
.input-file-button{
  width: 120px;
  height: 40px;
  padding-top: 10px;
  margin-bottom: 20px;
  background-color:#2872F9;
  color: white;
  cursor: pointer;
  border-radius: 20px;
  font-size: 1.0em;
  text-align: center;
}
.input-file-button:hover{
  background: #0084ff;
}

/* ,    */
.main-position-container{
  padding: 20px;
  position: absolute;
  top: 150px;
  width: 95%;
  height: 450px;
  margin: 0 auto;
}

/* ,     */
.profile-container-box{
  margin: 0 auto; 
  color: white;
  width: 105%;
}

/*      */

/*      */
.col-one{
  position: relative;
  vertical-align: top;
  display: inline-block;
  padding-right: 40px;
  width: 150px;
  font-size: 1.0em;
}

/*    */
.intro-cont-align{
  display: inline-block;
  font-size: 0.8em;
}

/*     */
.profile-image-input{
  position: relative;
  display: inline-block;
  height: 160px;
  width: 160px;
  background:#5E5E5E;
  border-radius: 20px;
  z-index: 11;
  margin-right: 15px;
  margin-bottom: 15px;
}

/*       */
.profile-image-change{
  position: relative;
  display: inline-block;
  height: 160px;
  width: 160px;
  background: no-repeat;
  margin: 0 75px 15px 80px;
  background-size:cover;
  border-radius: 20px;
  z-index: 13;
}

/*    */
.profile-nick-input{
  position: relative;
  display: inline-block;
  width: 380px;
  height: 50px;
  border-radius: 20px;
  background-color: #5E5E5E;
  border: none;
  outline: none;
  resize: none;
  color: white;
  font-size: 1.0em;
  margin-right: 15px;
  margin-bottom: 15px;
  padding: 15px;
}

/*     */
.profile-introduce-input{
  position: relative;
  width: 380px;
  height: 100px;
  border-radius: 20px;
  background-color: #5E5E5E;
  resize: none;
  color: white;
  font-size: 1em;
  margin-bottom: 15px;
  margin-right: 15px;
  padding: 15px;
  border: none;
  outline: none;
}

/*  */
#mypage_main-canc{
  transform: translate(350%,0%);
  width: 120px;
  height: 40px;
  border-radius: 15px;
  background-color: #5E5E5E;
  color: white;
  font-size: 1.0em;
  margin-right: 30px;
  margin-top:0;
  cursor: pointer;
}

/*   */
#mypage_main-save{
  transform: translateX(455%);
  width: 120px;
  height: 40px;
  border-radius: 15px;
  background-color: #2872F9;
  color: white;
  font-size: 1.0em;
  cursor: pointer;
  transition: 0.2s all ease;
}

.mypage_main-savebtn{
  position: relative;
  /* width: 120px; */
  height: 50px;
  border-radius: 15px;
  background-color: #2872F9;
  color: white;
  font-size: 1.0em;
  cursor: pointer;
  transition: 0.2s all ease;
  padding-left: 20px;
  padding-right: 20px;
}

.mypage_main-save_img{
  display: inline-block;
  vertical-align: top;
  width: 90px;
  height: 40px;
  border-radius: 15px;
  background-color: #2872F9;
  color: white;
  font-size: 1.0em;
  cursor: pointer;
  transition: 0.2s all ease;
}

.mypage_main-save:hover{
  background: #0084ff;
}
</style>