<template>
  <div>
    <div class="sideBar">
      <div class="side_search">
        <img src="../../assets/icons/magnifier.png" class="side_icon" />
        <input type="text" />
      </div>
      <div>
        <div class="group" id="main">
          <span> </span>
        </div>
        <div class="sel" v-for="(item, i) in sideArrays" :key="i">
          <div @click="clickEvent(i, $event, item)" id="element">
             {{ item.name }}
          </div>
        </div>
      </div>
    </div>
    <router-view />
    <div class="Temp_LogoutButton" @click="logout()"><button class="mypage_logout"></button></div>
  </div>
</template>

<script>
import axios from '../../axios';
export default {
  name: "MypageSide",
  data() {
    return {
      myStep: " ",
      sideArrays: [
        {name : " ",        url:"mymain"},
        {name : " ",    url:"mycard"},
        {name : "  ",     url:"mypost"},
        {name : "  ",     url:"myreview"},
        {name : " /  ", url:"prefer"},
        {name : " ",          url:"myalarm"},
        {name : " ",          url:"mywithdrawal"},
      ],
      clickNum: 0,
    };
  },
  mounted() {
    //   
    let id = document.querySelectorAll("#element")
    id[this.clickNum].style.backgroundColor = "#2872f9"
      //  
    this.$router.push('/mypage/mymain');
  },
  methods: {
    clickEvent(index, event, item) {
    //     
      this.myStep = item.name;
      let id = document.querySelectorAll("#element");
      event.target.style.backgroundColor = "#2872f9";
      if (this.clickNum != null) {
        if (this.clickNum != index) {
          id[this.clickNum].style.backgroundColor = "#2c2c2c";
        }
      }
      this.clickNum = index;
      //  
      this.$router.push('/mypage/' + item.url);
    },

    logout() {
      axios.get("/api/auth/logout").then((result) => {
        if (result.data == "ok") {
          this.$store.commit("userLogin", null);
          this.$router.push("/");
        } else {
          this.$store.commit("gModalOn", {
            msg: "ERR :  ",
            size: "normal",
          });
        }
      })
    }
  },
};
</script>

<style>
.sideBar {
  font-size: 1.2em;
  display: flex;
  flex-direction: column;
  padding: 50px 10px 0 10px;
  background: #2c2c2c;
  width: 300px;
  height: 100vh;
  position: fixed;
  top: 0px;
  left: 140px;
}
.group {
  cursor: pointer;
}
/* -------------------------------------------------------------------- */
.sel div {
  width: 73%;
  height: 40px;
  color: white;
  padding: 5px 10px 5px 20px;
  text-align: left;
  margin: 10px 0 0 50px;
  border-radius: 30px;
  font-weight: 600;
  font-size: 1em;
  cursor: pointer;
}

.Temp_LogoutButton {
  position: absolute;
  left: 160px;
  top: 90%;
}

.mypage_logout {
  width: 100px;
  height: 50px;
  border-radius: 10px;
  background: #2872f9;
  color: white;
}
</style>