<template>
  <div class="tests">
    <header class="header">
        <div class="service">
            <img class="icon" src="@/assets/icons/white/megaphone.png" alt="community">
            <span class="title"></span>
            <span class="topic" v-if="writeModify == false"> - </span>
            <span class="topic" v-if="writeModify == true"> - </span>
        </div>
    </header>  
    <div class="write_section">
      <div class="write_title">
        <input id="input" type="text" :value="`${noticeData.title}`" v-if="writeModify == true"/>
        <input id="input" type="text" v-if="writeModify == false"/>
      </div>
      <div class="write_content">
        <Editor :noticeData="noticeData" :writeModify="writeModify" @up="content = $event"/>
      </div>
      <div class="editer_info">
        !     .
        <br>    ''   .
        <br>   ' TOPIC'   .
    </div>
    </div>
    <div class="notice_btn_area">
      <div class="strong_btn" @click="pushEvent(1)"><span> </span></div>
      <div class="write_btn" @click="pushEvent(0)">
        <span v-if="writeModify == false"></span>
        <span v-if="writeModify == true"></span>
      </div>
      <div class="cancle_btn" @click="$emit('writebtn', 'cancle')"><span></span></div>
    </div>
  </div>
</template>

<script>
import Editor from './NoticeEditor'
export default {
  name: "OnWrite",
  data() {
    return {
      writeData : {
        emphasis : 0,
        title: '',
        content: '',
        date : '',
      },
    }
  },
  components: {
      Editor
  },
  props:{
    noticeData:Object,
    writeModify:Boolean,
  },
  methods:{
    pushEvent(emphasis){
      // 
      this.writeData.title = document.getElementById('input').value;

      // 
      this.writeData.content = this.content;

      // 
      var today = new Date().toISOString();
      this.writeData.date = today;

      //  
      this.writeData.emphasis = emphasis;

      const writeData = {type: 'arrUp', content : this.writeData};
      this.$emit('writebtn', writeData);
    }
  }
};
</script>

<style>
.write_section {
  margin: 0 auto;
  padding: 10px 15px;
  width: 95%;
  height: 75%;
  background-color: #2c2c2c;
  border-radius: 20px;
  position: relative;
  top: 50px;
}
.write_title {
  margin: 5px auto;
  width: 100%;
  height: 7%;
  background-color: #5e5e5e;
  border-radius: 20px;
  padding: 0 10px;
}
.write_title input {
  position: relative;
  left: 5px;
  width: 99%;
  height: 100%;
  border: none;
  background: none;
  color: white;
}
.write_content {
  margin: 0 auto;
  width: 100%;
  height: 77%;
  background-color: #5e5e5e;
  border-radius: 20px;
}
.write_content textarea {
  background: none;
  border: none;
  position: relative;
  top: 5px;
  resize: none;
  width: 100%;
  height: 95%;
}
.editer_info{
  color: white;
  margin: 5px 0 0 0;
  font-size: 0.8em;
}
.notice_btn_area {
  width: 100%;
  text-align: center;
  color: white;
  display: flex;
  justify-content: center;
  position: relative;
  top: 70px;
}
.strong_btn {
  padding: 5px 10px;
  background-color: red;
  width: 100px;
  border-radius: 20px;
  margin: 0 10px;
}
.write_btn {
  padding: 5px 10px;
  background-color: #2872f9;
  width: 100px;
  border-radius: 20px;
  margin: 0 10px;
}
.cancle_btn {
  padding: 5px 10px;
  margin: 0 10px;
  background-color: #5e5e5e;
  width: 100px;
  border-radius: 20px;
}
</style>